@import url('https://fonts.googleapis.com/css2?family=B612:ital,wght@1,700&display=swap');

.profile-logo {
    height: 80vmin;
    pointer-events: none;
}

.home-content {
    
    height: 100vh;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

.brand-text p {
    font-size: 3rem;
    font-family: 'B612', sans-serif;
    margin: 10px;
}

@media screen and (max-width: 480px) {
    .profile-logo {
        height: 110vmin;
    }
    .home-content {
        flex-direction: column;
    }
}